
  import { Component, OnInit, ViewEncapsulation } from '@angular/core';
  // import { LookupService, AssetService } from 'src/app/api/services';
  import { CookieService } from 'ngx-cookie-service';
  import { ActivatedRoute } from '@angular/router';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { OperationalStatusReasonComponent } from '../operational-status-reason/operational-status-reason.component';
@Component({
  selector: 'app-operational-maintanance-list',
  templateUrl: './operational-maintanance-list.component.html',
  styleUrls: ['./operational-maintanance-list.component.scss']
})
export class OperationalMaintananceListComponent implements OnInit {


	public subCategoryList: any = [];
	public assetList: any = [];
	public assetId: any;
	public search: any = {};
	public maintList:any = [{},{}];
	public statusData:any =[];

	constructor(
		private route: ActivatedRoute,
		private cookieService: CookieService,
		private lookupService:LookupService,
		public dialog: MatDialog
	) { }


	ngOnInit(): void {
		this.getStatusList();
		// this.route.paramMap.subscribe(params => {
		// 	if (params) {
		// 		this.assetId = params.get('id');
		// 	}
		// })
	
		
	}
	getStatusList() {
		let queryParamBase: any = {};
		queryParamBase = {
			ApartmentId: this.cookieService.get('apartmentId'),
			LookupTypeId: 190,
		};		
		this.lookupService.getLookupValueByLookupTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
      this.statusData = res?res:[];
			}
		});
  }
	
  addComments(value): void {	
	const dialogRef = this.dialog.open(OperationalStatusReasonComponent, {
		// width: '50%',
		panelClass: 'material',
		data: {value:value}
	});

	dialogRef.afterClosed().subscribe(result => {
		console.log('The dialog was closed');
		if (result) {
		   console.log(result);
		}
		
	});
}

}
