import { Component, OnInit ,ViewChild} from '@angular/core';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';

@Component({
  selector: 'app-maintain-operational-maintanance',
  templateUrl: './maintain-operational-maintanance.component.html',
  styleUrls: ['./maintain-operational-maintanance.component.scss']
})
export class MaintainOperationalMaintananceComponent implements OnInit {
  listOfFiles: any;
  isMaintainedbyStaff:any =true;
  fileList: any;
  selectedFile: any;
  isFileAdded: boolean;
  asset:any ={};
  ckeditor:any ;
  message:any={};
  subCategoriesData:any =[];
  categoriesData:any =[];
  maintain:any ={};
 
  constructor(
    private genMaintService:GenMaintService
  ) { }

  ngOnInit(): void {
   this.getAllCategory();
   this.getAllSubCategory();
  }


  uploadFile(event){
    this.isFileAdded = true;

    for (var i = 0; i <= event.target.files.length - 1; i++) {
      var selectedFile = event.target.files[i];
      this.fileList.push(selectedFile);
      this.listOfFiles.push({'name':selectedFile.name, 'size':selectedFile.size})
    }

  }

  deleteFile(index){
    // Delete the item from fileNames list
    this.listOfFiles.splice(index, 1);
    // delete file from FileList
    this.fileList.splice(index, 1);

    this.selectedFile = null;

    if(this.fileList.length == 0 && this.listOfFiles.length == 0){
      this.isFileAdded = false;
    }

  }
  onContentChanged(event){
    
  }
  getAllCategory() {
		this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
			if (res) {
				this.categoriesData = res ? res : [];
			
			}
		});
	}
  getAllSubCategory() {
		this.genMaintService.getAllGenMaintenanceSubCategory().subscribe((res: any) => {
			if (res) {
				this.subCategoriesData = res ? res : [];
			
			}
		});
	}

  
}
